<template>
  <div class="wrap w100 h100">
    <el-tabs tab-position="left" class="w100 h100">
      <el-tab-pane label="ripple：">
        <div class="ripple-container container">
          <div class="element-button">
            <div class="group">
              <el-button v-ripple icon="el-icon-search" circle></el-button>
              <el-button
                v-ripple="ripple"
                icon="el-icon-search"
                circle
              ></el-button>
            </div>
            <el-divider></el-divider>
            <div class="group">
              <el-button v-ripple plain class="ripple">朴素按钮</el-button>
              <el-button v-ripple round plain class="ripple"
                >朴素按钮</el-button
              >
            </div>
            <el-divider></el-divider>
            <div class="group">
              <el-button v-ripple type="primary" round>主要按钮</el-button>
              <el-button v-ripple="ripple" type="success" round
                >成功按钮</el-button
              >
            </div>
          </div>
          <el-divider></el-divider>
          <div class="group custom-wrap">
            <div>
              <span class="title">默认颜色：</span>
              <span v-ripple class="ripple button ripple-style">click</span>
            </div>
            <div>
              <span class="title"
                >自定义颜色：
                <el-color-picker v-model="ripple.color"></el-color-picker
              ></span>
              <span v-ripple="ripple" class="ripple button ripple-style"
                >click</span
              >
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="particle：">
        <div class="particle-container container">
          <div class="element-button">
            <div class="group">
              <el-button v-particle icon="el-icon-search" circle></el-button>
              <el-button
                v-particle="particle"
                icon="el-icon-search"
                circle
              ></el-button>
            </div>
            <el-divider></el-divider>
            <div class="group">
              <el-button v-particle plain class="ripple">朴素按钮</el-button>
              <el-button v-particle round plain class="ripple"
                >朴素按钮</el-button
              >
            </div>
            <el-divider></el-divider>
            <div class="group">
              <el-button v-particle type="primary" round>主要按钮</el-button>
              <el-button v-particle="particle" type="success" round
                >成功按钮</el-button
              >
            </div>
          </div>
          <el-divider></el-divider>
          <div class="group custom-wrap">
            <div>
              <span class="title">
                <el-button @click="drawer = true" type="text">
                  点我打开自定义
                </el-button>
              </span>
            </div>
            <div>
              <span v-particle="particle" class="ripple ripple-style"
                >click</span
              >
            </div>
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <el-drawer
      :with-header="false"
      :visible.sync="drawer"
      direction="rtl"
      custom-class="demo-drawer"
      ref="drawer"
    >
      <div class="demo-drawer__content">
        <el-form>
          <el-form-item label="颜色：">
            <el-color-picker v-model="particle.color"></el-color-picker>
          </el-form-item>
          <el-form-item label="数量：">
            <el-input-number
              v-model="particle.amount"
              :min="1"
              :max="100"
            ></el-input-number>
          </el-form-item>
          <el-form-item label="尺寸：">
            <el-input-number
              v-model="particle.size"
              :min="1"
              :max="100"
            ></el-input-number>
          </el-form-item>
          <el-form-item label="过渡时间：">
            <el-input-number
              v-model="particle.duration"
              :min="1"
              :max="100000"
            ></el-input-number>
          </el-form-item>
          <el-form-item label="类型：">
            <el-radio-group v-model="particle.type" size="small">
              <el-radio-button label="circle">circle</el-radio-button>
              <el-radio-button label="triangle">triangle</el-radio-button>
              <el-radio-button label="rectangle">rectangle</el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="执行方向：">
            <el-radio-group v-model="particle.direction" size="small">
              <el-radio-button label="left">left</el-radio-button>
              <el-radio-button label="right">right</el-radio-button>
              <el-radio-button label="top">top</el-radio-button>
              <el-radio-button label="bottom">bottom</el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="绘制形状：">
            <el-radio-group v-model="particle.style" size="small">
              <el-radio-button label="stroke">stroke</el-radio-button>
              <el-radio-button label="fill">fill</el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="是否为周期：">
            <el-switch
              v-model="particle.once"
              active-color="#13ce66"
              inactive-color="#00A0FF"
            >
            </el-switch>
          </el-form-item>
          <el-form-item label="是否隐藏对象：">
            <el-switch
              v-model="particle.hidden"
              active-color="#13ce66"
              inactive-color="#00A0FF"
            >
            </el-switch>
          </el-form-item>
          <el-form-item label="时间曲线：">
            <el-select v-model="particle.easing" placeholder="请选择">
              <el-option
                v-for="item in easingList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      drawer: false,
      easingList: [
        {
          label: "easeOutQuart",
          value: "easeOutQuart",
        },
        {
          label: "easeOutQuad",
          value: "easeOutQuad",
        },
        {
          label: "easeInExpo",
          value: "easeInExpo",
        },
        {
          label: "easeInQuint",
          value: "easeInQuint",
        },
        {
          label: "easeInCirc",
          value: "easeInCirc",
        },
        {
          label: "easeInCubic",
          value: "easeInCubic",
        },
        {
          label: "easeOutSine",
          value: "easeOutSine",
        },
      ],
      ripple: {
        plain: true,
        transition: 0.5,
        color: "rgba(113,71,117,1)",
      },
      particle: {
        once: false,
        hidden: true,
        type: "rectangle",
        style: "stroke",
        size: 15,
        amount: 3,
        color: "#007CCC",
        duration: 600,
        easing: "easeOutQuad",
        oscillationCoefficient: 5,
        particlesAmountCoefficient: 2,
        direction: "right",
      },
    };
  },
};
</script>
 
<style  >
</style>



